:root {
  --section-border: var(--border-primary);
}

.section__heading {
  position: relative;
  padding: 16px 0;
  border-left: var(--section-border);
  border-right: var(--section-border);
}
.section__title {
  font-size: 20px;
  line-height: 1.3;
  color: var(--accent-primary-color);
}
.section__content {
  border-top: var(--section-border);
}
@media (min-width: 768px) {
  .section__heading {
    padding: 40px 0;
  }
  .section__title {
    font-size: 36px;
    line-height: 1.16;
  }
}

:root {
  --services-border: var(--border-primary);
  --services-border-radius: var(--border-radius-primary);
  --services-white-color: var(--white-color);
}

.services__items-wrap {
  border-left: var(--services-border);
  border-right: var(--services-border);
}
.services__item {
  padding: 12px 12px 20px;
  position: relative;
}
.services__item:after {
  content: "";
  position: absolute;
  bottom: 0;
  left: -15px;
  width: 100vw;
  height: 1px;
  background-color: hsl(40, 17%, 77%);
}
.services__item:hover, .services__item:focus-visible {
  background-color: var(--services-white-color);
}
.services__img {
  border-radius: var(--services-border-radius);
  width: 100%;
  height: 228px;
}
.services__item-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 10px;
}
.services__stroke {
  display: grid;
  place-items: center;
  width: 40px;
  height: 40px;
  border-radius: 50%;
}
.services__stroke:hover, .services__stroke:focus-visible {
  border: 1px solid var(--accent-primary-color);
}
@media (min-width: 768px) {
  .services__inner {
    border-bottom: var(--services-border);
  }
  .services__items-wrap {
    display: flex;
  }
  .services__item {
    padding: 28px 28px 30px;
  }
  .services__item + .services__item {
    border-left: var(--services-border);
  }
  .services__item::after {
    content: none;
  }
  .services__title {
    font-size: 24px;
    line-height: 1.5;
  }
}
@media (min-width: 1024px) {
