  height: 7px;
  background-color: hsla(162, 63%, 34%, 0.15);
  border-radius: 14px;
}
.budget-range .range-grid__green-spectrum {
  position: relative;
  display: inline-block;
  width: 154px;
  height: 7px;
  background-color: var(--accent-primary-color);
  border-radius: 14px;
}
.budget-range .range-grid__green-spectrum::before {
  position: absolute;
  content: "";
  display: inline-block;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background-color: var(--white-color);
  border: 1px solid var(--main-color);
  top: calc(50% - 6px);
  left: calc(50% - 6px);
}
.budget-range .range-grid__values {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.budget-range .range-grid__values p {
  font-size: 12px;
  font-weight: 500;
  flex-basis: 88px;
  text-align: center;
}
@media (min-width: 768px) {
  .budget-range__heading {
    gap: 16px;
    margin-bottom: 24px;
  }
  .budget-range__budget-range__min, .budget-range__budget-range__max {
    flex-basis: 252px;
  }
  .budget-range__content {
    padding: 20px;
    gap: 24px;
  }
  .budget-range__value {
    font-size: 24px;
  }
  .budget-range__tooltip {
    font-size: 16px;
    padding: 16px;
    max-width: 256px;
  }
  .budget-range__banner {
    padding: 16px;
    background-position: left 12px top 22px;
  }
  .budget-range .range-grid__light-spectrum {
    width: 120px;
    height: 10px;
  }
  .budget-range .range-grid__green-spectrum {
    width: 212px;
    height: 10px;
  }
  .budget-range .range-grid__green-spectrum::before {
    width: 16px;
    height: 16px;
    top: calc(50% - 8px);
    left: calc(50% - 8px);
  }
  .budget-range .range-grid__values p {
    font-size: 16px;
    flex-basis: 120px;
  }
}